<template>
	<el-row class="brandWarp">
    <el-col :span="24" class="warp-breadcrum1">
      <div class="grid-content bg-purple-dark">
        <span><i class="i_col_red">{{withTotalAmt}}</i></span>
        <button class="margin_l btn_col" @click="withDrawHandler"></button>
      </div>
      <div class="curOrder"></div>
      <ul class="sel_wrap">
        <li>
          <div class="block">
            <span class="demonstration"></span>
            <el-date-picker
              v-model="value"
              type="daterange"
              placeholder="" @change="chooseDate">
            </el-date-picker>
          </div>
        </li>
<!--         <li>
          <div class="block">
            <span class="demonstration"></span>
            <span class="nav_select margin_r_30" v-for="(item, index) in selectDate" :key="index" :class="{active_nav: dateStat === index}" @click="choose(index)">{{item}}</span>
          </div>
        </li> -->
        <li>
          <div class="block">
            <span class="demonstration"></span>
            <span class="nav_select margin_r_30" v-for="(item, index) in classify" :key="index" :class="{active_nav: classStat === index}"  @click="selClass(item,index)">{{item}}</span>
          </div>
        </li>
      </ul>
      <div class="grid-content bg-purple-dark wallet_title">
        <span><i class="i_col_blue">{{houstonJZ}}</i></span>
        <span class="margin_l"><i class="i_col_red">{{withdrawalsTX}}</i></span>
      </div> 
    </el-col>
    <el-col :span="24" class="warp-main" style="margin: auto;margin-bottom:100px;height:280px;">
      <el-table :data="tableData" align="center" border style="width: 100%">
        <el-table-column prop="cashSuTime" align="center" label="">
          <template scope="scope">
            <span v-if="scope.row.cashSuTime">{{new Date(scope.row.cashSuTime).getFullYear()+'-'+ fillZero(new Date(scope.row.cashSuTime).getMonth()+1)+'-'+fillZero(new Date(scope.row.cashSuTime).getDate())}}</span>
            <span v-if="scope.row.updated">{{new Date(scope.row.updated).getFullYear()+'-'+ fillZero(new Date(scope.row.updated).getMonth()+1)+'-'+fillZero(new Date(scope.row.updated).getDate())}}</span>
          </template>
        </el-table-column>
        <el-table-column  prop="getMoney" align="center" label="">
          <template scope="scope">
            <span class="col_blue">{{scope.row.getMoney}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="cashMoney" align="center" label="">
          <template scope="scope">
            <span class="col_red">{{scope.row.cashMoney}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="anumber" align="center" label="">
        </el-table-column>
        <el-table-column prop="handler" align="center" label="">
          <template scope="scope">
            <el-button
              size="mini"
              type="info"
              @click="queryDetail(scope.$index, scope.row)"></el-button>
          </template>  
        </el-table-column>
      </el-table>
    </el-col>
    <!--  -->
    <el-dialog
      :visible.sync="withDrawSets"
      size="tiny" style="text-align:center;">
      <i class="el-icon-information" style="color:#fbb308;font-size:30px;margin-bottom:10px;"></i>
      <div></div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="withDrawSets = false"> </el-button>
        <el-button type="primary" @click="gotoSet"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      :visible.sync="statTip"
      size="tiny" style="text-align:center;">
      <i class="el-icon-information" style="color:#fbb308;font-size:30px;margin-bottom:10px;"></i>
      <div>~~</div>
    </el-dialog>

    <!--  -->
    <el-dialog
      :visible.sync="detailVisible"
      size="small">
      <div class="detail_title"></div>
      <div class="custInfo"></div>
      <div class="custContent"><b></b><span>111</span><b style="margin-left:80px;"></b><span>222</span></div>
      <div class="custInfo"></div>
      <table class="datail_tb">
        <tr><td colspan="7"><span class="pad_l_30">2017-06-08</span><span class="pad_l_30"></span></td></tr>
        <tr class="trs">
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr class="trs" v-for="(item, index) in orderDetail" :key="index">
          <td>{{item.goodsName}}</td>
          <td>{{item.price}}</td>
          <td>{{item.num}}</td>
          <td>{{item.totalPrice}}</td>
          <td :rowspan="infoList.length" v-if="index == 0">123</td>
          <td :rowspan="infoList.length" v-if="index == 0"></td>
          <td :rowspan="infoList.length" v-if="index == 0">123</td>
        </tr>
      </table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="detailVisible = false"> </el-button>
        <el-button type="primary" @click="detailVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="withDrawBank"
      size="small">
      <div style="margin:0 auto;width:380px;">
        <el-row>
          <el-col :span="24" align="center"><div class="i_red i_title"></div></el-col>
        </el-row>
        <el-row>
          <el-col :span="24" align="center"><div class="i_title"><span>{{openName}} &nbsp;&nbsp;&nbsp;&nbsp;{{postalType}}{{bankNo}}</span></div></el-col>
        </el-row>
        <el-form style="padding-top:10px;" :model="WithDrawForm" :rules="rulesWithDraw" ref="WithDrawForm">
          <el-form-item label="" prop="withDrawAccount">
            <el-input v-model.number="WithDrawForm.withDrawAccount" class="item_w_input fl"></el-input>
          </el-form-item>
          <el-form-item label="" style="padding-left:14px;">
            <el-input v-model="WithDrawForm.withDrawPhone" class="item_w_input fl" :disabled="true"></el-input>
          </el-form-item>
          <div style="margin-bottom:16px;">
            <span class="fl" style="padding-left:15px;"></span>
            <el-input v-model="WithDrawForm.rg_code" class="item_c_input fl" style="margin-left:12px;"></el-input>
            <button v-if="hYzm" class="btn_col" @click.prevent="hasYzm()">{{Yzm}}</button>
            <button v-else class="btn_col" style="background-color: #C8C8C8;" disabled>{{Yzm1}}</button>
          </div>
        </el-form>
        <div style="margin-top:30px;">
          <button class="withDrawBtn btn_col" @click="applyHandler('WithDrawForm')"></button>
          <el-button class="withDrawBtn1" @click="withDrawBank = false"> </el-button>
        </div>
      </div>
    </el-dialog>
	</el-row> 
</template>

<script>
  import global from '../../global/global'
  import util from '../../../common/util'
  export default {
    data(){
      var checkAmt = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        setTimeout(() => {
          if (!this.isAmt(value)) {
            callback(new Error(''));
          } else {
            if (value > this.withTotalAmt) {
              callback(new Error(''));
            } else {
              callback()
            }
          }
        }, 1000);
      };
      return {
        value: null,
        value: '',
        hYzm: true,
        tableData: [],
        classify: ['','',''],
        classStat: 0,
        withDrawSets: false,
        statTip: false,
        withDrawBank: false,
        withDrawBank01:false,
        detailVisible: false,
        withTotalAmt: 700,
        WithDrawForm: {
          withDrawAccount: '',
          withDrawPhone: global.getSalesUser().phone,
          rg_code: '',
        },
        rulesWithDraw: {
          withDrawAccount: [
            { validator: checkAmt, trigger: 'blur' }
          ]
        },
        Yzm1: '',
        Yzm: '',
        postalType: '',
        trueName: '',
        openName: '',
        bankName: '',
        bankNo: '',
        infoList: [],
        houstonJZ: '',//
        withdrawalsTX: '',//
        orderDetail: null
      }
    },
    props: ['toMySon'],
    created: function() {
      var that = this;
      if (that.toMySon.isActive == true) {
        that.withDrawBank = that.toMySon.isActive
      }
      that.queryInfo();
      that.getMyWallet();
    },
    methods: {
      // 
      isAmt: function(str) {
        var reg = /^[0-9]+(.[0-9]{1,2})?$/;
        if (reg.test(str)) {
            return true;
        } else {
            return false;
        }
      },
      fillZero: function(n){
        return n<10 ? '0'+ n: n 
      },
      // 
      hasYzm: function() {
        var that = this;
        var mb = /^(((13[0-9]{1})|(15[0-9]{1})|(17[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
        var sec = 60;
        if (that.WithDrawForm.withDrawPhone == '' || !mb.test(that.WithDrawForm.withDrawPhone)) {
          that.$message.error('');
          return false
        } else {
          var obj = { phone: that.WithDrawForm.withDrawPhone }
          that.global.axiosPostReq('/witManage/gitVcode', obj).then((res) => {
            if (res.data.callStatus === 'SUCCEED') {
              for(let i=0; i<=60; i++) {
                window.setTimeout(function(){
                  if (sec != 0) {
                    that.hYzm = false;
                    that.Yzm1 =   sec + "" ;
                    sec--;
                  } else {
                    sec = 60;//  
                    that.hYzm = true;
                    that.Yzm = '';
                  }
                }, i * 1000)
              }
            } else {
              that.$message.error('');
            }
          })
        }
      },
      //
      gotoSet: function(){
        var that = this;
        var editWithDraw = {
          isActive: true,
          flag: 'editWithDraw',
        }
        that.$emit('msgFromChild',editWithDraw);
        this.withDrawSets = false
      },
      //
      queryInfo: function(){
        let params = {
          phone: global.getSalesUser().phone,
          token: global.getSalesToken()
        }
        global.axiosGetReq('/saleInfo/query',params).then((res) => {
          if(res.data.callStatus === 'SUCCEED'){
            console.log('---------------',res.data.data)
            this.postalType = res.data.data.postalType
            this.openName = res.data.data.openName
            this.trueName = res.data.data.trueName
            this.bankName = res.data.data.bankName
            this.bankNo = res.data.data.accountNumber
          }
        })
      },
      //
      getMyWallet: function() {
        var that = this;
        var obj = {
          token: 2,
          state: 0,
        }
        that.global.axiosPostReq('/myWallet/detail',obj).then((res) => {
          if (res.data.callStatus === 'SUCCEED') {
            this.houstonJZ = res.data.data.houstonJZ
            this.withdrawalsTX = res.data.data.withdrawalsTX
            // this.tableData = res.data.data
          } else {
            that.$message.error('');
          }
        })
      },
      // 
      chooseDate: function() {
        var that = this;
        var startDate = util.formatDate.format(new Date(that.value[0]));
        var endDate = util.formatDate.format(new Date(that.value[1]));
        // var obj = {
        //   token: that.global.getSalesToken(),
        //   state: 0,
        // }
        // that.global.axiosPostReq('/myWallet/detail',obj).then((res) => {
        //   if (res.data.callStatus === 'SUCCEED') {
        //     console.log(res)
        //     // that.tableData = res.data.data;
        //     console.log(that.tableData)
        //   } else {
        //     that.$message.error('');
        //   }
        // })
      },
      // 
      selClass: function(item,index){
        var that = this;
        that.classStat = index;
        if (item == '') {
          var obj = {
            token: that.global.getSalesToken(),
            state: 0,
          }
          that.global.axiosPostReq('/myWallet/detail',obj).then((res) => {
            if (res.data.callStatus === 'SUCCEED') {
              that.tableData = res.data.data;
              console.log(that.tableData)
            } else {
              that.$message.error('');
            }
          })
        } else if (item == '') {
          var obj = {
            token: that.global.getSalesToken(),
            state: 1,
          }
          that.global.axiosPostReq('/myWallet/detail',obj).then((res) => {
            if (res.data.callStatus === 'SUCCEED') {
              that.tableData = res.data.data;
              console.log(that.tableData)
            } else {
              that.$message.error('');
            }
          })
        } else if (item == '') {
          var obj = {
            token: that.global.getSalesToken(),
            state: 2,
          }
          that.global.axiosPostReq('/myWallet/detail',obj).then((res) => {
            if (res.data.callStatus === 'SUCCEED') {
              that.tableData = res.data.data;
              console.log(that.tableData)
            } else {
              that.$message.error('');
            }
          })
        }
      },
      queryDetail(index, row){
        if(row.cashId){
          //
          let params = {
            cashId: row.cashId,
            token: global.getSalesToken()
          }
          console.log('',params)
          global.axiosPostReq('/myWallet/queryOrder',params).then((res) => {
            if (res.data.callStatus === 'SUCCEED') {
              // this.orderDetail = res.data.data
              // console.log(res.data.data)
            } else {
              that.$message.error('');
            }
          })
        }else if(row.orderId){
          //
          let params = {
            orderId: row.orderId,
            token: global.getSalesToken()
          }
          console.log('',params)
          global.axiosPostReq('/myWallet/queryOrder',params).then((res) => {
            if (res.data.callStatus === 'SUCCEED') {

              console.log(res.data.data)
            } else {
              that.$message.error('');
            }
          })
        }
        
        this.detailVisible = true
      },
      withDrawHandler(){
        if(this.postalType ===''){
          this.withDrawBank = true
        }else if(this.postalType ===''){
          this.withDrawBank = true
        }else if(this.postalType === null){
          this.withDrawSets = true
        }       
      },
      applyHandler(formName){

        this.$refs[formName].validate((valid) => {
          if (valid) {
            let params = {
              token: global.getSalesToken(),
              phone: global.getSalesUser().phone,
              type: this.postalType,
              cashMoney: this.WithDrawForm.withDrawAccount,
              trueName: this.trueName,
              openName: this.openName,
              bankName: this.bankName,
              anumber: this.bankNo,
              vCode: this.WithDrawForm.rg_code
            }
            //
            if(!this.WithDrawForm.rg_code){
              this.$message.error('');
              return false
            }
            console.log('--------------',params)
            global.axiosPostReq('/witManage/submitWit',params).then((res) => {
              if (res.data.callStatus === 'SUCCEED') {
                this.statTip = true
                this.WithDrawForm.withDrawAccount = ''
                this.WithDrawForm.rg_code = ''
                this.withDrawBank = false
              } else {
                that.$message.error('');
              }
            })
          } else {
            return false;
          }
        });
        
      }
    }
  }
</script>

<style scope="scope">
  .brandWarp{
    width: 1200px;
    margin: auto;
  }
  .fl{
    float:left;
  }
  .fr{
    float: right;
  }
