    content:"";
    clear:both;
    display:block;
  }
  .i_col_red{
    color: red;
    font-weight: bold;
    font-style: normal;
  }
  .col_red{
    color: red;
  }
  .i_col_blue{
    color: #20a0ff;
    font-weight: bold;
