  }
  .withDrawBtn{
    width: 100px;
    margin-left: 82px;
  }
  .withDrawBtn1{
    width: 100px;
    margin-left: 58px;
  }

  .custInfo{
    height: 30px;
    line-height: 30px;
    padding-left: 24px;
    border-bottom: 1px dotted #ccc;
    background: url(../../../images/salesman/arrow.png) 0 8px no-repeat;
  }
  .detail_title{
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 16px;
    background: url(../../../images/salesman/titlebg.png) 400px 34px no-repeat;
  }
  .el-form-item__error{
    margin-left: 92px;
  }
  .datail_tb{
    width: 100%;
    border-collapse: collapse;/*   */
    border: none;
    margin-top: 20px;
  }
  .custContent{
    height: 70px;
    line-height: 70px;
    padding-left: 52px;
  }
  .datail_tb tr{
    width: 100%;
    height: 30px;
    line-height: 30px;
    border: 1px solid #ccc;
  }
  .trs td{
    width: 100px;
    text-align: center;
    border: 1px solid #ccc;
  }
</style>


